@font-face {
  font-family: "CodeProBoldLC";
  src: url("../fonts/CodeProBoldLC.otf") format("opentype");
}
@font-face {
  font-family: "BaseticaPro-Med";
  src: url("../fonts/BaseticaPro-Med.otf") format("opentype");
}
@font-face {
  font-family: "CamingoDosProCd-SemiBold";
  src: url("../fonts/CamingoDosProCd-SemiBold.otf") format("opentype");
}
@font-face {
  font-family: "Gotham-Bold";
  src: url("../fonts/Gotham-Bold.otf") format("opentype");
}
header {
  background-color: transparent;
}

nav {
  height: 15rem;
  margin: 0 10rem;
}

.container-fluid div a {
  color: #02490b;
  font-weight: bold;
}
.container-fluid div .navbar-right {
  font-weight: bold;
}
.container-fluid div .navbar-center a {
  margin: 0 1.5rem;
  border-bottom: 3px solid transparent;
  transition: all 1s;
}
.container-fluid div .navbar-center a:hover {
  border-bottom: 3px solid #02490b;
}
.container-fluid div button {
  margin: 0 2rem;
}
.container-fluid div a, .container-fluid div button {
  font-size: 1.5rem;
}
@media (min-width: 481px) {
  .container-fluid div a, .container-fluid div button {
    font-size: 1.75rem;
  }
}
@media (min-width: 768px) {
  .container-fluid div a, .container-fluid div button {
    font-size: 2rem;
  }
}
@media (min-width: 1025px) {
  .container-fluid div a, .container-fluid div button {
    font-size: 2.5rem;
  }
}

.navbar-nav .nav-link.active {
  color: #02490b;
}

.navbar-right {
  display: flex;
}

.intro {
  margin: 3rem 10rem;
}
.intro .intro-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 5rem;
}
.intro .intro-grid > * {
  margin: 5rem;
}
.intro .intro-grid-text h2 {
  font-size: 5rem;
}
.intro .intro-grid-text p {
  font-size: 2rem;
  margin: 3rem 0;
}
.intro .intro-grid-text .intro-small-p {
  font-size: 1rem;
  margin: 0.5rem 0;
  color: #a9adaa;
}
.intro .intro-grid-text a {
  margin: 10rem 0;
}
.intro .intro-grid-text a button {
  background-color: #0ae227;
  margin-bottom: 4rem;
}
.intro .intro-brands .intro-brands-title {
  text-align: center;
  text-transform: uppercase;
  color: #a9adaa;
}
.intro .intro-brands .intro-brands-images {
  display: flex;
  justify-content: space-between;
}
.intro .intro-brands .intro-brands-images img {
  width: 100%;
}

